div {
  box-sizing: border-box;
}

p {
  margin: 0;
}

.App {
  text-align: center;
}

ul {
  list-style: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input {
  border-width: 0;
}

input:focus {
  outline: none;
}

.input-container {
  display: flex;
  margin-top: 20px;
}

.input-container div {
  margin: auto;
}

.credit-input {
  width: 40%;
  margin-top: 5px;
  padding: 10px;
  background-color: whitesmoke;
  border-radius: 5px;
  text-align: center;
}

.credit-input:focus {
  background-color: wheat;
}

.result-container {
  padding-top: 400px;
  display: flex;
  margin-top: 20px;
}

.result-container div {
  margin: auto;
}

.credit-result {
  width: 100%;
  margin-top: 5px;
  padding: 10px;
  background-color: whitesmoke;
  border-radius: 5px;
  text-align: center;
}